          {
              //console.log("inside -> arr[" + i + "] = " + arr[i] + "     first == " + first)          
              //console.log("inside -> i == " + i); 
              first = arr[i]; 
              first_index = i;
          } 
          //console.log("arr[" + i + "] = " + arr[i] + "    first = " + first)
      }
