      this.propertyColor[second_index] = "warning";


      
      this.showTopResult = 1;
      if(first_index == 0) { 
          // safety - säkerhet
          this.primaryText = "<h2>Mitt primära drivande behov är <b>trygghet</b></h2>\
            <h4>Min övertygelse</h4>\
            Trygghet är viktigt för mig samtidigt som jag måste säkerställa att jag kommer fortsätta ha det tryggt och säkert i framtiden. Viktigt att jag undvika saker och situationer som gör det obekvämt eller obehagligt. Jag kan ha svårt att fokusera på att uppleva glädje om jag känner osäkerhet om vad som kommer att hända framöver.\
            <h4>Vad min övertygelse bidrar till och gör för mig/h4>\
            Jag undviker gärna att ta risker och jag planerar mina framtida steg omtänksamt och säkert - det är inte helt ovanligt att jag skriver ner en ”To Do” lista. Jag är varsam och tar väl hand om mig själv. Andra i min omgivning vet att jag är förutsägbar och jag brukar strukturerar min vardag.\
            <h4>Insikter jag kan gå miste om</h4>\
            Det är omöjligt att sia om framtiden då allt vi upplever existerar i nuet. Om jag inte tar några risker kan jag heller ej vinna något. Det är fullt möjligt att vara glad och trygg i nuet även om framtiden känns oviss.\
            <h4>Konsekvenserna av de insikter jag går miste om</h4>\
            Risken är att jag begränsar mig själv för nya upplevelser och lärorika händelser. Det är svårt för mig att engagera mig i andra människor med rädslan eller misstänksamheten att de kommer orsaka mig något jag inte kommer gilla. Min omgivning kan ibland uppfatta mig som en person med stort kontrollbehov och jag kan också upplevas som en aningen oentusiastisk och tråkig. Jag är oftast förutsägbar i motsats till att vara spontan och impulsiv.\
            <br><br>";
            this.thirdText = "<br><br>\
            <h2>Mina karaktäristiska drag</h2>\
            <h4>Mitt fokus</h4>\
